import { Grid, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import React from 'react'

const useStyles = makeStyles({
    label: {
        marginBottom: 10
    },
    container: {
        display: "flex",
        flexWrap: "wrap"
    },
    chip: {
        backgroundColor: "#e0e9f5",
        padding: "10px 20px",
        borderRadius: 10,
        fontSize: 14,
        color: "#1967d2",
        margin: 7
    }
})
const tags = ["First", "Second", "Third", "Fourth", "Fifth", "Sixth", "Seventh"]

const TagPart = () => {
    const classes = useStyles()
    return (
        <Grid container direction="column" style={{ marginTop: 40 }}>
            <Typography className={classes.label}>Tags</Typography>
            <div className={classes.container}>
                {tags.map((tag, index) => <div key={index} className={classes.chip}>{tag}</div>)}
            </div>
        </Grid>
    )
}

export default TagPart