export default function GitHubLabel({ options, onChange, value }) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  //   const [value, setValue] = React.useState([labels[1], labels[11]]);
  const [pendingValue, setPendingValue] = React.useState([]);
  const theme = useTheme();

  const handleClick = (event) => {
    // setPendingValue(value);
    if (Boolean(anchorEl)) {
      setAnchorEl(null);
    } else {
      setAnchorEl(event.currentTarget);
    }
  };

  const handleClose = (event, reason) => {
    if (reason === "toggleInput") {
      return;
    }
    // onChange(pendingValue);
    // if (anchorEl) {
    //   anchorEl.focus();
    // }
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "github-label" : undefined;
  const icon = <CheckBoxOutlineBlankOutlined fontSize="small" />;
  const checkedIcon = <CheckBox fontSize="small" />;

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div className={classes.select} onClick={handleClick}>
          {value.length === 0 ? (
            <div className={classes.placeholderContainer}>
              <span className={classes.placeholderLabel}>Location</span>
            </div>
          ) : (
            <div className={classes.activeSelect}>
              <span>Location</span>
              <span className={classes.count}>{value?.length}</span>
            </div>
          )}
          <span className={classes.endIcon}>
            {open ? (
              <ExpandLess
                className={classes.iconStyle}
                style={{ color: !isEmpty(value) ? "#fff" : "#000" }}
              />
            ) : (
              <ExpandMore
                className={classes.iconStyle}
                style={{ color: !isEmpty(value) ? "#fff" : "#000" }}
              />
            )}
          </span>
        </div>
        {/* {value.map((label) => (
          <div
            key={label.name}
            className={classes.tag}
            style={{
              backgroundColor: label.color,
              color: theme.palette.getContrastText(label.color),
            }}
          >
            {label.name}
          </div>
        ))} */}
      </div>
      <Popper
        id={id}
        open={open}
        anchorEl={anchorEl}
        placement="bottom-start"
        className={classes.popper}
      >
        {/* <div className={classes.header}>Apply labels to this pull request</div> */}
        <Autocomplete
          open
          onClose={handleClose}
          multiple
          classes={{
            paper: classes.paper,
            option: classes.option,
            popperDisablePortal: classes.popperDisablePortal,
          }}
          value={value}
          onChange={(event, newValue) => {
            onChange(newValue);
          }}
          disableCloseOnSelect
          disablePortal
          //   renderTags={() => null}
          noOptionsText="No labels"
          renderOption={(option, { inputValue, selected }) => {
            const matches = match(option, inputValue);
            const parts = parse(option, matches);
            return (
              <>
                <Checkbox
                  checked={selected}
                  color="primary"
                  style={{ paddingBottom: 0, paddingTop: 0 }}
                />
                <span
                  style={{
                    fontSize: 13,
                    fontWeight: 400,
                    paddingBottom: 0,
                    paddingTop: 0,
                  }}
                >
                  {parts.map((part, index) => (
                    <span
                      key={index}
                      style={{ fontWeight: part.highlight ? 600 : 300 }}
                    >
                      {part.text}
                    </span>
                  ))}
                </span>
              </>
            );
          }}
          options={options}
          //   getOptionLabel={(option) => option.name}
          renderInput={(params) => (
            <InputBase
              ref={params.InputProps.ref}
              inputProps={params.inputProps}
              autoFocus
              className={classes.inputBase}
            />
          )}
        />
      </Popper>
    </React.Fragment>
  );
}
