import { Button } from "@material-ui/core";
import { HighlightOff } from "@material-ui/icons";
import React, { useContext } from "react";
import FilterContext from "../../../context/FilterContext";
import { isEmpty } from "../../../utils/empty";

const ClearFilterButton = () => {
  const { reset, filters, selectedFacets, query } = useContext(FilterContext);
  return (
    <div style={{ textAlign: "center", marginLeft: "-22px" }}>
      <Button
        variant="text"
        color="primary"
        onClick={reset}
        style={{ textTransform: "capitalize" }}
        disabled={
          !Object.values(filters).some((val) => !isEmpty(val)) &&
          !Object.values(selectedFacets).some((val) => !isEmpty(val)) &&
          isEmpty(query)
        }
      >
        Clear
      </Button>
    </div>
  );
};

export default ClearFilterButton;
