import { Grid } from "@material-ui/core";
import React, { useContext, useEffect } from "react";
import FilterContext from "../../context/FilterContext";
import Item from "./Item";

const ItemContainer = ({
  sort,
  perPage,
  page,
  activeDetail,
  setActiveDetail,
  toggleView,
}) => {
  const { hits, loading } = useContext(FilterContext);
  useEffect(() => {
    if (!loading) {
      setActiveDetail(hits?.[0]);
    }
  }, [hits]);
  return (
    <Grid
      container
      alignItems="center"
      justify="space-between"
      style={{ marginTop: 10 }}
    >
      {hits
        ?.sort((a, b) => {
          if (sort === "desc") {
            return (
              new Date(a.created_at).getTime() -
              new Date(b.created_at).getTime()
            );
          } else {
            return (
              new Date(b.created_at).getTime() -
              new Date(a.created_at).getTime()
            );
          }
        })
        .slice(page * perPage - perPage, page * perPage)
        ?.map((hit) =>
          hit.id ? (
            <Item
              key={hit?.id}
              job_id={hit?.id}
              slug={hit?.slug}
              employer_link={hit?.employer_link}
              title={hit?._formatted?.title}
              postOn={hit?.posted_on}
              employer={hit?._formatted?.employer}
              logo={hit?.logo}
              salary={hit?.salary}
              salaryType={hit?.salary_type}
              location={hit?._formatted?.address}
              workType={hit?.work_type}
              createdAt={hit?.created_at}
              isActive={activeDetail?.id === hit?.id}
              province={hit?.province}
              region={hit?.region}
              onClick={() => {
                setActiveDetail(hit);
                toggleView();
              }}
            />
          ) : (
            <></>
          )
        )}
    </Grid>
  );
};

export default ItemContainer;
