export const detectDevice = () => {
  if (window) {
    const width = window.innerWidth;
    if (width >= 1024) {
      return "desktop";
    } else if (width > 767 && width < 1024) {
      return "tablet";
    } else if (width < 576) {
      return "mobileLg";
    }
  } else {
    return false;
  }
};
