const SkillFilter = () => {
  const { facets, setSelectedFacets, selectedFacets } =
    useContext(FilterContext);

  //   console.log("facets***", facets.experience);

  const handleChange = (e, newValue) => {
    const value = newValue?.map((value) => {
      return `experience:${value}`;
    });
    setSelectedFacets((prev) => ({ ...prev, experience: value }));
  };

  const classes = useStyles();
  const getValue = () => {
    return !isEmpty(selectedFacets?.experience)
      ? selectedFacets?.experience?.map((experience) =>
        experience.replace("experience:", "")
      )
      : [];
  };

  const sorter = (data) => {
    let stringData = []
    let numberData = []
    let otherData = []
    data.forEach(da => {
      var regExp = /[a-zA-Z]/g;
      var regExp2 = /[0-9][-][0-9]/g;
      if (regExp.test(da)) {
        stringData.push(da)
      }
      else if (regExp2.test(da)) {
        numberData.push(da)
      }
      else {
        otherData.push(da)
      }
    })

    return stringData.sort().concat(numberData.sort()).concat(otherData.sort())
  }


  return (
    <Grid container>
      <DropDown
        value={getValue()}
        placeholder="Experience"
        options={sorter(Object.keys(facets?.experience || {}))}
        onChange={(e) => handleChange(null, e.target.value)}
      />

      {/* <Autocomplete
                multiple
                className={classes.input}
                options={Object.keys(facets?.experience || {})}
                disableCloseOnSelect
                onChange={handleChange}
                popupIcon={<ExpandMore style={{ fontSize: 18 }} />}
                limitTags={0}
                PaperComponent={(props) => <Paper  {...props} style={{ minWidth: 300 }} />}
                renderOption={(option, { inputValue }) => {
                    const matches = match(option, inputValue);
                    const parts = parse(option, matches);

                    return (<React.Fragment>
                        <CheckBox
                            checked={selectedFacets?.experience?.some((val) =>
                                val.includes(option)
                            )}
                            label={
                                parts.map((part, index) => (
                                    <span
                                        key={index}
                                        style={{ fontWeight: part.highlight ? 600 : 300 }}
                                    >
                                        {part.text}
                                    </span>
                                ))
                            }
                            name={option}
                            count={facets?.experience[option]}
                        />
                    </React.Fragment>
                    )
                }}
                renderInput={(params) => (
                    <TextField {...params} InputProps={{
                        ...params.InputProps,
                        classes: { notchedOutline: classes.noBorder },

                    }} variant="outlined" placeholder="Skill" />
                )}
            /> */}
    </Grid>
  );
};

export default SkillFilter;
