import { Grid } from "@material-ui/core";
import React, { useContext, useState } from "react";
import FilterContext from "../../context/FilterContext";
import HeaderPart from "./HeaderPart";
import ItemContainer from "./ItemContainer";
import Pagination from "./Pagination";

const DisplayPart = ({ activeDetail, setActiveDetail, toggleView }) => {
  const [sort, setSort] = useState("asc");
  const [perPage, setPerPage] = useState(10);
  const { hits, page, setPage } = useContext(FilterContext);
  return (
    <Grid container direction="column" alignItems="flex-start">
      <HeaderPart
        page={page}
        totalHits={hits?.filter((hit) => hit?.id)?.length}
        sort={sort}
        setSort={setSort}
        perPage={perPage}
        setPerPage={setPerPage}
      />
      <Grid item xs={12} style={{ marginTop: 10 }}>
        <div id="getJobAlert"></div>
      </Grid>
      <ItemContainer
        sort={sort}
        perPage={perPage}
        page={page}
        activeDetail={activeDetail}
        setActiveDetail={setActiveDetail}
        toggleView={toggleView}
      />
      {hits?.length > 0 && (
        <Pagination
          selectedPage={page}
          setPage={setPage}
          count={Math.ceil(hits?.length / perPage)}
        />
      )}
    </Grid>
  );
};

export default DisplayPart;
