const sorter = (data) => {
  let stringData = [];
  let numberData = [];
  let otherData = [];

  data.forEach((da) => {
    var regExp = /[a-zA-Z]/g;
    var regExp2 = /[0-9][-][0-9]/g;

    if (regExp2.test(da)) {
      numberData.push(da.split("-")[0]);
    }
  });

  return numberData.sort();
};

const secondValueSorter = (data) => {
  let stringData = [];
  let numberData = [];
  let otherData = [];
  // console.log("data***", data);
  data.forEach((da) => {
    var regExp = /[a-zA-Z]/g;
    var regExp2 = /[0-9][-][0-9]/g;

    if (regExp2.test(da)) {
      numberData.push(da.split("-")[1]);
    }
  });

  return numberData.sort();
};

const SalaryFilter = () => {
  const {
    reset,
    facets,
    setSelectedFacets,
    selectedFacets,
    setFilters,
    filters,
  } = useContext(FilterContext);
  const [open, setOpen] = useState(false);
  const [getMin, setMin] = useState("");
  const [getMax, setMax] = useState(null);
  const [getMaxError, setMaxError] = useState("");
  const [getMinError, setMinError] = useState("");
  const [getMergeValue, setMergeValue] = useState("");
  const classes = useStyles();

  var newOpen = false;

  const handleClose = (e) => {
    setOpen(false);
    newOpen = true;
  };
  const handleClick = (e) => {
    if (Number(getMin) < Number(getMax)) {
      setMaxError("");
      setMinError("");
      console.log("****e****", e);
      setOpen(false);
      newOpen = true;
      if (getMin === 0 || (getMin > 0 && getMax === 0) || getMax > 0) {
        var newValue = `salary:${getMin}-${getMax}`;
        console.log("***value", newValue);
        let newFilter = filters;
        newFilter.salary = `(salary >= ${getMin}) AND (salary <= ${getMax})`;
        setFilters({ ...newFilter });
        // setSelectedFacets((prev) => ({ ...prev, salary: [newValue] }));
      }
    } else {
      if (Number(getMin) > Number(getMax)) {
        setMaxError("Please select More value than min value.");
      } else {
        setMinError("Please select less value than max value.");
      }
    }
  };
  const handleChange = (e, newValue) => {
    if (e === "min") {
      setMin(newValue);
    } else if (e === "max") {
      setMax(newValue);
    }
  };

  useEffect(() => {
    if (getMin === 0 || (getMin > 0 && getMax === 0) || getMax > 0) {
      setMergeValue(`${getMin}-${getMax}`);
    }
  }, [getMin, getMax]);

  useEffect(() => {
    if (isEmpty(filters.salary)) {
      setMin("");
      setMax("");
      setMergeValue("");
    }
  }, [filters.salary]);

  return (
    <Grid container>
      <Select
        IconComponent={(props) => (
          <ExpandMore
            style={{
              fontSize: 18,
              marginRight: 10,
              marginTop: 3,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",

              color: !isEmpty(getMergeValue) ? "#fff" : "#000",
            }}
            {...props}
          />
        )}
        className={`${classes.select} ${
          !isEmpty(getMergeValue) ? classes.active : null
        }`}
        displayEmpty
        open={open}
        MenuProps={MenuProps}
        value={getMergeValue}
        onChange={(e) => {
          handleChange(e);
        }}
        onClick={(e) => {
          if (newOpen) {
            setOpen(false);
          } else {
            setOpen(true);
          }
        }}
        input={<OutlinedInput />}
        renderValue={(selected) => {
          if (isEmpty(selected)) {
            return <span style={{ color: "#8b8b8b" }}>Salary</span>;
          }
          return (
            <div
              style={{ fontSize: 14, display: "flex", alignItems: "center" }}
            >
              <span>{getMergeValue}</span>
            </div>
          );
        }}
        inputProps={{ "aria-label": "Without label" }}
      >
        <div>
          <SalaryDropDown
            options={secondValueSorter(Object.keys(facets?.salary || {}))}
            setOpen={setOpen}
            setMin={setMin}
            getMinError={getMinError}
            getMin={getMin}
            handleChange={handleChange}
            handleClose={handleClose}
            setMax={setMax}
            getMaxError={getMaxError}
            getMax={getMax}
            handleClick={handleClick}
          />
        </div>
      </Select>
    </Grid>
  );
};

export default SalaryFilter;
