import { Button, Grid, Typography, useMediaQuery } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import {
  AccessTime,
  BusinessCenterOutlined,
  ChevronRight,
  MonetizationOnOutlined,
} from "@material-ui/icons";
import React from "react";
import Parser from "html-react-parser";
import moment from "moment";

const useStyles = makeStyles({
  container: {
    backgroundColor: "#fff",
    borderRadius: 10,
    marginBlock: 10,
    padding: "10px 20px",
    cursor: "pointer",
  },
  subContainer: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start",
  },
  img: {
    width: 58,
    height: 59,
  },
  detailContainer: {
    display: "flex",
    flexDirection: "column",
    marginLeft: 20,
  },
  title: {
    fontWeight: 700,
    fontSize: 18,
  },
  labelWithIcon: {
    fontSize: 12,
    alignItems: "center",
    display: "flex",
    marginRight: 10,
    marginTop: 15,
  },
  button: {
    borderRadius: 10,
    // backgroundColor: "#1967d2",
    textTransform: "capitalize",
    marginLeft: "auto",
  },
  empoyerHead: {
    color: "#1967d2",
  },
  grayText: {
    color: "#888",
    fontSize: 13,
    fontWeight: 500,
  },
  orangeText: {
    color: "#FF9000",
    fontSize: 13,
    fontWeight: 500,
  },
  isActive: {
    backgroundColor: "#fafafa",
  },
});

const findString = (data) => {
  var regExp2 = /[0-9][-][0-9]/g;
  if (regExp2.test(data)) {
    return true;
  } else {
    return false;
  }
};

const Item = ({
  job_id,
  title,
  employer,
  employer_link,
  postOn,
  location,
  salary,
  salaryType,
  logo,
  slug,
  workType,
  createdAt,
  isActive,
  onClick,
  province,
  region,
}) => {
  const classes = useStyles();
  const formatUnix = moment.unix(createdAt).format();
  const post = moment(formatUnix).fromNow();
  const matches = useMediaQuery("(min-width:500px)");
  // console.log("salary***", findString(salary));
  return (
    <Grid
      className={`${classes.container} ${
        isActive ? classes.isActive : null
      } item_left_side`}
      item
      xs={12}
      onClick={onClick}
    >
      <div className={classes.subContainer}>
        <img className={classes.img} src={logo} alt={postOn} />
        <div className={classes.detailContainer}>
          <Typography className={`${classes.title} highlighted`}>
            {title ? Parser(title) : ""}
          </Typography>
          <Typography
            variant="subtitle2"
            className={`highlighted-2 ${classes.grayText}`}
            style={{ textAlign: "left" }}
          >
            Posted by{" "}
            <span className={classes.empoyerHead}>
              <a
                style={{ textDecoration: "none", color: "#0d51a2" }}
                href={employer_link}
              >
                {employer ? Parser(employer) : ""}
              </a>
            </span>{" "}
            • {post}
          </Typography>
          <div className={classes.subContainer}>
            <Typography variant="body2" className={classes.orangeText}>
              {findString(salary) ? "$" + salary : salary} / {salaryType}
            </Typography>
            •
            <Typography
              variant="body2"
              className="highlighted-2"
              style={{ fontSize: 13 }}
            >
              {province} {","} {region}
            </Typography>
          </div>
        </div>
        {matches ? (
          <div className="detail_arrow_wraper">
            <Button
              variant="text"
              className={classes.button}
              onClick={onClick}
              style={{ background: "none" }}
            >
              <ChevronRight style={{ background: "none" }} />
            </Button>
          </div>
        ) : null}
      </div>
    </Grid>
  );
};

export default Item;
