import {
  Checkbox,
  FormHelperText,
  InputBase,
  MenuItem,
  OutlinedInput,
  Select,
} from "@material-ui/core";
import { makeStyles, styled } from "@material-ui/core/styles";
import { ExpandMore } from "@material-ui/icons";
import React, { useContext } from "react";
import FilterContext from "../../../context/FilterContext";
import { isEmpty } from "../../../utils/empty";

const MenuProps = {
  getContentAnchorEl: () => null,
  anchorOrigin: {
    vertical: "bottom",
    horizontal: "center",
  },
  transformOrigin: {
    vertical: "top",
    horizontal: "center",
  },
  PaperProps: {
    style: {
      maxHeight: 48 * 4.5 + 8,
      width: 250,
    },
  },
};

const useStyles = makeStyles({
  select: {
    border: "none",
    // padding: 10,
    borderRadius: 10,
    outline: "none",
    height: 45,
    width: "100%",
    ".MuiOutlinedInput-notchedOutline": {
      borderColor: "#ECECEC",
    },

    fontSize: 13,
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      border: "1px solid #ccc",
    },
    "& .MuiSelect-select:focus": {
      outline: "none",
      backgroundColor: "transparent",

      boxShadow: "none",
    },
  },
  active: {
    backgroundColor: "#0d51a2",
    color: "#fff",
    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
      border: "none",
    },
  },

  count: {
    backgroundColor: "#fff",
    borderRadius: 50,
    width: 20,
    height: 20,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
    color: "#0d51a2",

    marginLeft: 5,
    fontWeight: 600,
    fontSize: 12,
  },
});
const DropDown = ({
  value,
  onChange,
  placeholder,
  options,
  error = "",
  type = 0,
}) => {
  const classes = useStyles();
  const { setPage } = useContext(FilterContext);

  // console.log("placeholder", placeholder);
  return (
    <>
      <Select
        IconComponent={(props) => (
          <ExpandMore
            style={{
              fontSize: 18,
              marginRight: 10,
              marginTop: 3,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",

              color: !isEmpty(value) ? "#fff" : "#000",
            }}
            {...props}
          />
        )}
        className={`${classes.select} ${
          !isEmpty(value) ? classes.active : null
        }`}
        displayEmpty
        multiple={type ? false : true}
        // input={<BootstrapInput />}
        MenuProps={MenuProps}
        value={value}
        onChange={(e) => {
          onChange(e);
          setPage(1);
        }}
        input={<OutlinedInput />}
        renderValue={(selected) => {
          if (isEmpty(selected)) {
            return <span style={{ color: "#8b8b8b" }}>{placeholder}</span>;
          }
          return (
            <div
              style={{ fontSize: 14, display: "flex", alignItems: "center" }}
            >
              <span>{placeholder}</span>
              {type ? (
                <span className={classes.count}>1</span>
              ) : (
                <span className={classes.count}>{selected?.length}</span>
              )}
            </div>
          );
        }}
        inputProps={{ "aria-label": "Without label" }}
      >
        {/* <FormHelperText>Error</FormHelperText> */}
        <MenuItem style={{ fontSize: 13, fontWeight: 400 }} disabled value="">
          {placeholder}
        </MenuItem>

        {options?.map((option) => {
          return (
            <MenuItem key={option} value={option}>
              {type ? (
                <></>
              ) : (
                <Checkbox
                  checked={value.includes(option)}
                  color="primary"
                  style={{ paddingBottom: 0, paddingTop: 0 }}
                />
              )}

              <span
                style={{
                  fontSize: 13,
                  fontWeight: 400,
                  paddingBottom: 0,
                  paddingTop: 0,
                }}
              >
                {option}
              </span>
            </MenuItem>
          );
        })}
      </Select>
      {error ? <FormHelperText>{error}</FormHelperText> : ""}
    </>
  );
};

export default DropDown;
