import {
  Box,
  Button,
  CircularProgress,
  Container,
  createMuiTheme,
  Divider,
  Drawer,
  Grid,
  IconButton,
  ThemeProvider,
  useMediaQuery,
} from "@material-ui/core";
import { esES } from "@material-ui/core/locale";
import axios from "axios";
import React, { useContext, useEffect, useRef, useState } from "react";
import CONFIG from "./config/scopo";
import FilterContext from "./context/FilterContext";
import { isEmpty } from "./utils/empty";
import "./newdesign.css";
import { getQueryString } from "./utils/queryString";
import SearchPart from "./components/LastesDesign/SearchPart";
import FilterPart from "./components/LastesDesign/FilterPart";
import DisplayPart from "./components/LastesDesign/DisplayPart";
import LocationFilter from "./components/Filters/LatestDesign/LocationFilter";
import CategoryFilter from "./components/Filters/LatestDesign/CategoryFilter";
import JobTypeFilter from "./components/Filters/LatestDesign/JobTypeFilter";
import SalaryFilter from "./components/Filters/LatestDesign/SalaryFilter";
import SalaryTypeFilter from "./components/Filters/LatestDesign/SalaryTypeFilter";
import SkillFilter from "./components/Filters/LatestDesign/SkillFilter";
import ClearFilterButton from "./components/Filters/LatestDesign/ClearFilterButton";
import DetailPart from "./components/LastesDesign/DetailPart";
import { detectDevice } from "./utils/detectDevice";
import { ArrowBack } from "@material-ui/icons";

var host = window.location.host;
if (host.includes("scopo.me")) {
  console.log("in scopo");
  CONFIG.MEILISEARCHURL = "https://scopoms.thatsmytask.com/indexes/jobs/search";
} else if (host.includes(".thatsmytask.com")) {
  console.log("in jobi");
  var subDomain = window.location.href.split("/")[3];
  CONFIG.MEILISEARCHURL = `https://jobims.thatsmytask.com/indexes/jobs-${subDomain}/search`;
} else {
  console.log("else local");
  CONFIG.MEILISEARCHURL = CONFIG.MEILISEARCHURL;
}

const LatestDesign = () => {
  const theme = createMuiTheme(
    {
      palette: {
        primary: { main: "#1976d2" },
      },
    },
    esES
  );
  const {
    setFacets,
    facets,
    setHits,
    filters,
    selectedFacets,
    setFilters,
    setSelectedFacets,
    query,
    setQuery,
    setLoading,
    locations,
    setLocations,
    hits,
    loading,
  } = useContext(FilterContext);
  const fetch = async () => {
    setLoading(true);
    const facetFilters = Object.values(selectedFacets).filter(
      (data) => !isEmpty(data)
    );

    console.log(facetFilters);
    const locationFilters = Object.values(filters).filter(
      (data) => !isEmpty(data)
    );

    const res = await axios.post(CONFIG.MEILISEARCHURL, {
      attributesToHighlight: CONFIG.ATTRIBUTETOHIGHLIGHT,
      // facetsDistribution: CONFIG.FACETDISTRIBUTION,
      limit: CONFIG.LIMIT,
      q: query,
      ...(!isEmpty(locationFilters) && {
        filters: locationFilters.join(" AND "),
      }),
      ...(!isEmpty(facetFilters) && {
        facetFilters,
      }),
    });

    if (res.status === 200) {
      setLoading(false);
      if (isEmpty(facets)) {
        setFacets(res.data?.facetsDistribution);
      }
      if (isEmpty(locations.region)) {
        Object.keys(filters).forEach((key) => {
          setLocations((prev) => ({
            ...prev,
            [key]: [
              ...new Set(
                res.data?.hits &&
                  res.data?.hits
                    ?.filter((d) => d && d[key] != null)
                    .map((option) => option[key])
              ),
            ],
          }));
        });
      }

      const newData = res.data?.hits.filter((item) => {
        if (item && item.is_active) return item;
      });
      // console.log("newData**", newData);
      setHits(newData);
    }
  };
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsDone(false);
    const params = getQueryString();
    // console.log(param)
    if (!isEmpty(params)) {
      params.location &&
        setFilters((prev) => ({
          ...prev,
          province: `(province='${params.location}')`,
        }));
      params.keyword && setQuery(params.keyword);
      params.category &&
        setSelectedFacets((prev) => ({
          ...prev,
          category: [
            ...prev.category,
            `category:${window.location.search
              .substring(1)
              .split("=")[3]
              .split("%20")
              .join(" ")}`,
          ],
        }));
    }
    setIsDone(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    isDone && fetch();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [filters, selectedFacets, query, isDone]);
  const matches = useMediaQuery("(min-width:500px)");
  const matchesTablet = useMediaQuery("(min-width:900px)");
  const [activeDetail, setActiveDetail] = useState({});
  const DetailPartRef = useRef(null);
  const [isMobileResponsive, setIsMobileResponsive] = useState(false);
  const [currentView, setCurrentView] = useState("sidebar");

  useEffect(() => {
    if (detectDevice() === "tablet" || detectDevice() === "mobileLg") {
      setIsMobileResponsive(true);
    }
  }, [window.innerWidth]);

  const isSideBar =
    !isMobileResponsive || (isMobileResponsive && currentView === "sidebar");

  const [openFilter, setOpenFilter] = useState(false);

  // console.log("currentView***", currentView);
  return (
    <ThemeProvider theme={theme}>
      {loading && (
        <div
          style={{
            position: "fixed",
            top: 0,
            width: "100%",
            height: "100%",
            left: 0,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            backgroundColor: "rgba(0,0,0,.5)",
            zIndex: 11,
          }}
        >
          <CircularProgress />
        </div>
      )}

      <div>
        <div className="searchinSection" style={{ height: "100%" }}>
          <Grid container>
            {currentView === "detail" ? (
              <IconButton
                aria-label="delete"
                onClick={() => {
                  setCurrentView("sidebar");
                }}
                size="medium"
                style={{ marginLeft: "2px" }}
              >
                <ArrowBack fontSize="inherit" />
              </IconButton>
            ) : (
              <></>
            )}
            <Grid
              item
              xs={12}
              className={matchesTablet ? "active-sticky" : ""}
              style={{
                ...(matchesTablet
                  ? {
                      position: "sticky",
                      top: 0,
                      backgroundColor: "#fff",
                      zIndex: 1,
                    }
                  : {}),
                display: !isSideBar ? "none" : null,
              }}
            >
              {!matchesTablet ? (
                <Drawer open={openFilter} onClose={() => setOpenFilter(false)}>
                  <div
                    style={{
                      padding: 10,
                      width: 250,
                      display: "flex",
                      flexDirection: "column",
                      height: "100%",
                    }}
                  >
                    <Box style={{ marginBlock: 5 }}>
                      <SearchPart />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <LocationFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <CategoryFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <JobTypeFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <SalaryFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <SalaryTypeFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <SkillFilter />
                    </Box>
                    <Box style={{ marginBlock: 5 }}>
                      <ClearFilterButton />
                    </Box>
                  </div>
                </Drawer>
              ) : (
                <div
                  style={{
                    display: "grid",
                    gridTemplateColumns: matchesTablet
                      ? matches
                        ? "2fr repeat(6,1fr) 55px"
                        : "1fr"
                      : !matches
                      ? "1fr"
                      : "repeat(2,1fr)",
                    alignItems: "center",
                    gap: 20,
                    flexWrap: "wrap",
                    paddingTop: 20,
                    paddingBottom: 20,
                  }}
                  className="main_search_wraper container"
                >
                  <SearchPart />
                  <LocationFilter />
                  <CategoryFilter />
                  <JobTypeFilter />
                  <SalaryFilter />
                  <SalaryTypeFilter />
                  <SkillFilter />
                  <ClearFilterButton />
                </div>
              )}

              {!matchesTablet ? (
                <Button onClick={() => setOpenFilter(true)} variant="outlined">
                  Filters
                </Button>
              ) : (
                <Divider
                  style={{ width: "100%", backgroundColor: "#ECECEC" }}
                />
              )}
            </Grid>
            <Grid container className="container">
              <Grid
                item
                md={5}
                xs={12}
                style={{
                  ...(matchesTablet
                    ? { borderRight: "1px solid #cccccc66", overflow: "auto" }
                    : {}),
                  paddingRight: 10,
                  paddingTop: 30,
                  height: matchesTablet ? "90vh" : "auto",
                  display: !isSideBar ? "none" : null,
                }}
              >
                {/* <FilterPart /> */}
                <DisplayPart
                  activeDetail={activeDetail}
                  setActiveDetail={setActiveDetail}
                  toggleView={() => {
                    if (!matchesTablet || !matches) {
                      setCurrentView("detail");
                    }
                  }}
                />
              </Grid>
              <Grid
                item
                md={7}
                xs={12}
                ref={DetailPartRef}
                className="job_listing_right_side"
                style={{
                  ...(matchesTablet ? { overflow: "auto" } : {}),
                  height: matchesTablet ? "90vh" : "auto",
                  display:
                    !isMobileResponsive ||
                    (isMobileResponsive && currentView === "detail")
                      ? null
                      : "none",
                }}
              >
                {!isEmpty(hits) && <DetailPart activeDetail={activeDetail} />}
              </Grid>
            </Grid>
          </Grid>
        </div>
      </div>
    </ThemeProvider>
  );
};

export default LatestDesign;
