const getArrayOrStringFromField = (field) => {
  return field && String(field)?.includes(",") ? field?.split(",") : field;
};

var host = window.location.host;
if (host.includes("scopo.me")) {
  console.log("in scopo");

  CONFIG.APIURL = "https://scopoms.thatsmytask.com/indexes/jobs/search";
} else if (host.includes(".thatsmytask.com")) {
  console.log("in jobi");
  var subDomain = window.location.href.split("/")[3];

  CONFIG.APIURL = `https://jobi.thatsmytask.com/${subDomain}/public/`;
} else {
  console.log("else local");
  CONFIG.APIURL = CONFIG.APIURL;
}

const findString = (data) => {
  var regExp2 = /[0-9][-][0-9]/g;
  if (regExp2.test(data)) {
    return true;
  } else {
    return false;
  }
};

