import { Checkbox, FormControlLabel, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";

const useStyles = makeStyles({
    checkbox: {
        "& > .MuiButtonBase-root > .MuiIconButton-label > input": {
            display: "none",
        },
        "& > .MuiFormControlLabel-label": {
            fontStyle: "normal",
            fontWeight: 600,
            fontSize: 14,
            fontFamily: "'Manrope', sans-serif",
            // lineHeight: 22,
            textAlign: "center",
            color: "#8b8b8b",
        },

    },
    checkBoxContainer: {
        width: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between"
    },
    count: {
        fontSize: 13,
        color: "#8b8b8b",
        paddingInline: 5,
    },
    active: {
        backgroundColor: "#e0e9f5",
        color: "#4483da",
        fontWeight: 600,
    }
});

const CheckBox = ({ name, handleChange, checked, label, count }) => {
    const classes = useStyles();
    return (
        <div className={classes.checkBoxContainer}>
            <FormControlLabel
                className={classes.checkbox}
                control={
                    <Checkbox
                        size="small"
                        color="primary"
                        name={name}
                        onChange={handleChange}
                        checked={checked}
                    />
                }
                label={label}
            />
            <Typography className={`${classes.count} ${checked ? classes.active : null}`}>{count}</Typography>
        </div>
    );
};

export default CheckBox;
