import React from "react";
import { usePagination } from "@material-ui/lab/Pagination";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import {
  ArrowBackOutlined,
  ArrowLeft,
  ChevronLeft,
  ChevronRight,
} from "@material-ui/icons";

const useStyles = makeStyles({
  ul: {
    listStyle: "none",
    margin: 0,
    display: "flex",
    borderRadius: 4,
    width: "100%",
    padding: "10px 0px",
    justifyContent: "center",
  },
  button: {
    cursor: "pointer",
    border: "0px !important",
    backgroundColor: "transparent",
    margin: 0,
    fontStyle: "normal",
    fontWeight: 500,
    fontSize: 14,
    display: "flex",
    alignItems: "center",
    textAlign: "center",
    color: "#000000",
    marginLeft: 6,
    padding: "10px 16px",
    fontFamily: "'Manrope', sans-serif",

    "&.active": {
      color: "#fff",
      backgroundColor: "#0D51A2",
      borderRadius: 50,
    },
    "&:disabled": {
      opacity: 0.5,
      cursor: "not-allowed",
    },
  },
});

export default function Pagination({ count, setPage, selectedPage }) {
  const classes = useStyles();
  const { items } = usePagination({
    count: count,
    page: selectedPage,
  });

  const action = (type, page) => {
    if (type === "previous") {
      setPage(page);
    } else {
      setPage(page);
    }
  };

  return (
    <Grid container>
      <nav style={{ width: "100%" }}>
        <ul className={`pagination_ul ${classes.ul}`}>
          {items.map(({ page, type, selected, ...item }, index) => {
            let children = null;
            if (type === "start-ellipsis" || type === "end-ellipsis") {
              children = "…";
            } else if (type === "page") {
              children = (
                <button
                  type="button"
                  className={`${classes.button} ${
                    page === selectedPage ? "active" : ""
                  }`}
                  {...item}
                  onClick={() => setPage(page)}
                >
                  {page}
                </button>
              );
            } else {
              children = (
                <button
                  type="button"
                  className={classes.button}
                  {...item}
                  onClick={() => action(type, page)}
                >
                  {type === "previous" ? (
                    <ChevronLeft style={{ fontSize: 20 }} />
                  ) : (
                    <ChevronRight style={{ fontSize: 20 }} />
                  )}
                </button>
              );
            }

            return <li key={index}>{children}</li>;
          })}
        </ul>
      </nav>
    </Grid>
  );
}
